@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300|Nunito:600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

html {
  font-size: 125%; }

body {
  color: #fff;
  font-family: 'Nunito Sans', sans-serif; }

header {
  background: -webkit-gradient(linear, left top, left bottom, from(#1570bf), to(#183f8c));
  background: -webkit-linear-gradient(#1570bf, #183f8c);
  background: linear-gradient(#1570bf, #183f8c);
  padding-bottom: 50px; }

.wrapper {
  width: 90%;
  max-width: 1600px;
  margin: 0 auto; }

main {
  padding: 50px 0; }

footer {
  background: #2c3e50;
  padding: 20px 0;
  text-align: center; }

h1,
h2,
h3 {
  font-family: 'Nunito', sans-serif;
  font-weight: 600; }

h2 {
  margin: 0; }

p {
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 300; }

img {
  width: 100%; }

.topBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px; }
  .topBar h1 {
    width: 250px;
    margin-right: 20px; }
    .topBar h1 img {
      width: 100%; }

.imageBox {
  display: flex;
  align-items: center;
  flex: 0 0 100px;
  width: 100px;
  height: 100px;
  margin-right: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background: #fff; }
  .imageBox img {
    max-width: 75px;
    max-height: 75px;
    margin: 10px auto; }

.infoList {
  list-style: none;
  margin: 10px auto;
  padding: 0;
  width: 100%;
  max-width: 500px; }
  .infoList li {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5); }
    .infoList li:last-of-type {
      border-bottom: none; }

.twoColumn {
  display: flex;
  align-items: flex-end; }
  .twoColumn .stockInfo {
    width: 35%; }
    .twoColumn .stockInfo .mainInfo {
      display: flex;
      justify-content: flex-start; }
    .twoColumn .stockInfo p {
      font-size: .8rem; }
    .twoColumn .stockInfo li {
      font-size: .8rem; }
      .twoColumn .stockInfo li span:first-of-type {
        font-weight: 600;
        font-family: 'Nunito', sans-serif; }
      .twoColumn .stockInfo li span:last-of-type {
        opacity: 0.8; }
  .twoColumn .stockChart {
    width: 65%;
    padding-left: 40px;
    margin-bottom: 20px; }

.react-autosuggest__container {
  position: relative; }

.react-autosuggest__input {
  width: 280px;
  height: 50px;
  padding: 10px 20px;
  font-weight: 300;
  border: none;
  border-radius: 25px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); }

.react-autosuggest__input:focus {
  outline: none; }

.react-autosuggest__container--open .react-autosuggest__input {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }

.react-autosuggest__suggestions-container {
  display: none; }

.react-autosuggest__container--open .react-autosuggest__suggestions-container {
  position: absolute;
  display: block;
  overflow: hidden;
  top: 50px;
  width: 280px;
  color: #000;
  background: rgba(255, 255, 255, 0.9);
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); }
  .react-autosuggest__container--open .react-autosuggest__suggestions-container .suggestion-content {
    display: flex;
    align-items: center;
    background-repeat: no-repeat; }
  .react-autosuggest__container--open .react-autosuggest__suggestions-container .highlight {
    color: #000;
    font-family: 'Nunito', sans-serif;
    font-weight: 600; }

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px; }

.react-autosuggest__suggestion:not(:first-child) {
  border-top: 1px solid #ddd; }

.react-autosuggest__suggestion--highlighted {
  background-color: #fff;
  color: #000; }

.react-autosuggest__suggestion--focused .highlight {
  color: #120000;
  font-family: 'Nunito', sans-serif;
  font-weight: 600; }

.newsFeed {
  display: flex;
  flex-wrap: wrap;
  margin: 20px -10px;
  align-items: flex-start;
  color: #2c3e50; }
  .newsFeed > p {
    text-align: center;
    width: 100%; }
  .newsFeed a {
    text-decoration: none;
    color: #2c3e50; }
  .newsFeed h2 {
    width: 100%;
    text-align: center;
    color: #2c3e50; }
  .newsFeed .article {
    flex: 1 1 250px;
    background: #fff;
    padding: 20px;
    margin: 10px;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); }
    .newsFeed .article .previewImage {
      margin: -20px -20px 0px;
      width: calc(100% + 40px);
      height: 0;
      overflow: hidden;
      padding-top: 75%;
      position: relative; }
    .newsFeed .article img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }

@media only screen and (max-width: 900px) {
  .twoColumn {
    flex-direction: column;
    align-items: center; }
    .twoColumn .stockInfo {
      width: 100%;
      max-width: 100%; }
      .twoColumn .stockInfo .mainInfo {
        justify-content: center; }
    .twoColumn .stockChart {
      width: 100%;
      padding: 0; }
  .descriptionAndList {
    display: flex; }
    .descriptionAndList > * {
      width: 50%;
      padding: 20px; } }

@media only screen and (max-width: 600px) {
  .topBar {
    flex-direction: column; }
  .descriptionAndList {
    flex-direction: column; }
    .descriptionAndList > * {
      width: 100%;
      padding: 0px; }
  .mainInfo {
    flex-direction: column; }
    .mainInfo .imageBox {
      margin: 10px auto; }
    .mainInfo div {
      text-align: center; } }

